<template>
		<view class="score-box">
			<view class="score" :class="{'small':size=='small'}">
				<view class="score-one" v-for="(star,k) in stars" :key="k">
						<view class="iconfont iconxingxing1"></view>
						<view :style="{ width: star.activeWitch.replace('%','')*star_width/100+'rpx'}" class="iconfont iconxingxing11"></view>
				</view>
				<view class="point">
					{{activeScore}}åˆ†
				</view>
			</view>
			
		</view>
</template>

<script>
	
	export default {
		data() {
			return {
				star_width:32
			}
		},
		props: {
			activeScore: {
				default: 0,
				type: Number
			},
			size:{
				default: 'middle',
				type: String
			}
		},
		created() {
			if(this.size=='small'){
				this.star_width=22
			}
		},
		computed: {
			stars() {
				const value = this.activeScore ? this.activeScore : 0;
				const starList = [];
				const floorValue = Math.floor(value);
				const ceilValue = Math.ceil(value);
				for (let i = 0; i < 5; i++) {
					if (floorValue > i) {
						starList.push({
							activeWitch: "100%"
						});
					} else if (ceilValue - 1 === i) {
						starList.push({
							activeWitch: (value - floorValue) * 100 + "%"
						});
					} else {
						starList.push({
							activeWitch: "0"
						});
					}
				}
				return starList;
			}
		},
		methods: {

		}
	}
</script>

<style lang="scss">
	.score-box {
		display: flex;

		.score {
			display: flex;
			align-items: center;
			justify-content: center;
			color:  $color-primary;
			.iconfont {
				font-size: 32rpx;
			}
			.score-one {
				position:relative;
				.iconxingxing11 {
					position: absolute;
					top: 0;
					width: 0%;
					overflow: hidden;
					
				}
			}
		}
		.score.small .iconfont {
			font-size: 22rpx;
		}
		.point {
			font-size: 20rpx;
			font-weight: 500;
			color: #444444;
			margin-left: 20rpx;
		}
	}
</style>
